@import url(https://fonts.googleapis.com/css?family=Concert+One&display=swap);
body {
  margin: 0;
  font-family: "Concert One", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
  color: white;
}

.App-container {
  min-height: 80vh;
  display: flex;
  justify-content: center;
}

.card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.box-item {
  width: 30vw;
  height: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.box-item span {
  font-size: 1.5em;
}

.dungeon-and-dragons {
  display: block;
  width: 300px;
  height: 300px;
  border-radius: 10px;
  border: 5px white solid;
  margin: 10px;
  background-image: url(/boardgames-club/static/media/clint-bustrillos-X-A-LJVAhzk-unsplash.76ec0be1.jpg);
  background-size: cover;
}

.boardgames {
  display: block;
  width: 300px;
  height: 300px;
  border-radius: 10px;
  border: 5px white solid;
  margin: 10px;
  background-image: url(/boardgames-club/static/media/robert-coelho-laNNTAth9vs-unsplash.7161c933.jpg);
  background-size: cover;
}
.box-content {
  -webkit-transition: -webkit-transform 0.2s ease-in;
  transition: -webkit-transform 0.2s ease-in;
  transition: transform 0.2s ease-in;
  transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
}
.box-content:hover {
  -webkit-transform: scale(1.05) rotate(5deg);
          transform: scale(1.05) rotate(5deg);
}

.App-header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(20px + 2vmin);
  color: white;
}

